@media screen and (min-width:767px) {

    .Header_container {
        height: 100px;
        padding:6vw
    }
    .header_right_content p{
      color: white;       
      font-family: "quest";
      font-size: 1.3vw;
      padding: 10px;
      transition: .3s;
      
     }
     .header_right_content p:hover{
      transform: scale(1.1);
      border-radius: 8px;
      box-shadow: 13px 13px 25px #13141d,-13px -13px 25px #2f3249
       ;

     }
   
     .icon{
        width: 50px;
        transition: .3s;
        cursor: pointer;
        
        
      }
      
      .icon1{
        mix-blend-mode: color;


      }
      .icon:hover{
        transform: scale(1.1);
      }

    .wishlist_Container1 {
        position: fixed;
        width: 100%;
        height: 100vh;
        left: 0;
        top: 0;
        backdrop-filter: blur(4px);
        z-index: 100;
        display: flex;
        justify-content: center;
        align-items: center
    }

    .logo {
        width: 100px;
    }
}
